import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Popper from "@mui/material/Popper";

const dispatchedEvents = [];

const SrsMuiPopper = function (props, ref) {
  return (
    <Popper {...props} theme={props.muiTheme}>
      <slot />
    </Popper>
  );
}

const styles = `
  /* Custom styles for the MuiPopper component */
`;

const props = [
  {
    name: "anchorEl",
    defaultValue: null,
    type: "object",
  },
  {
    name: "open",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "placement",
    defaultValue: "bottom-start",
    type: "string",
  },
  {
    name: "modifiers",
    defaultValue: [],
    type: "object",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Popper if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(React.forwardRef(SrsMuiPopper), props, "mui");
export { WrappedComponent, styles, props, dispatchedEvents, slots };
